import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

import { environment } from 'src/environments/environment';
import { RegisterModel } from 'src/core/models/registry/register.model';
import { LoginDto } from 'src/core/models/registry/dto\'s/login-dto.model';

@Injectable({
    providedIn: 'root'
})
export class RegisterService {
    constructor(private http: HttpClient) {
    }

    getRegister(value: LoginDto) {
        return this.http.get(`${environment.apiUrl}register/${value}`)
    }

    createRegister(value: RegisterModel) {
        return this.http.post(`${environment.apiUrl}register/`, value)
    }

    deleteRegister(value: number){
        return this.http.delete(`${environment.apiUrl}register/${value}`)
    }
}